import { getDevtoolsId, devtoolsAttribute } from '../package-settings';

function getDevtoolsProps(componentName) {
  return {
    [devtoolsAttribute]: getDevtoolsId(componentName),
  };
}

export { devtoolsAttribute, getDevtoolsProps, getDevtoolsId };
